import React, { useEffect, useState } from "react";
import backgroundimage from '../assets/media/auth/agency-dark.png'
import logo from '../assets/media/logos/logo.png'
import '../assets/plugins/global/plugins.bundle.css'
import '../assets/css/style.bundle.css'
import { Link, useNavigate, useLocation} from "react-router-dom";
import { ToastContainer, toast } from "react-toastify";
import $ from "jquery";
import {
    toasterrormsg,
    toastsuccessmsg,
    Post,
    apiHeader
} from "../config/Reausable";

function Resetpassword() {
    document.title = 'Buildify AI | Admin | Reset Password'

    /**
    * VALIDATION
    * DEFAULT VALUE SET
    * API CALL
    */

    //toggle password button
    useEffect(() => {
        $(".toggle-password").on("click", function () {
            $(this).toggleClass("fa-eye fa-eye-slash");
            var input = $($(this).attr("toggle"));
            if (input.attr("type") === "password") {
                input.attr("type", "text");
            } else {
                input.attr("type", "password");
            }
        });
        return () => {
            $(".toggle-password").off("click");
        };
    }, []);
    //toggle password button over

    //use state starts
    const location = useLocation();
    let adminId = location.state ? location.state : "" ;
    const [otp, setOTP] = useState("");
    const [password, setPassword] = useState("");
    const [confirmPassword, setConfirmPassword] = useState("");
    //use state over

    const navigate = useNavigate();
    if(adminId == ""){
        navigate("../forgotpassword");
    }
    //call API
    
    const resetpassword_api = async () => {
        //client side validation starts
        var error = 0
        if (otp.trim() == "") {
            error = 1;
            toasterrormsg("OTP Required");
        } else if (password.trim() == "") {
            error = 1;
            toasterrormsg("Password Required");
        } else if (confirmPassword.trim() == "") {
            error = 1;
            toasterrormsg("Confirm Password Required");
        } else if (confirmPassword.trim() != password.trim()) {
            error = 1;
            toasterrormsg("Confirm Password and Password Does Not Match");
        }
        if (error == 0) {

            const data = {
                adminId : adminId,
                otp: otp,
                password: password.trim(),
                confirmPassword: confirmPassword.trim()
            };
            const response = await Post('resetPassword', data, apiHeader(false))
            console.log("API Response: ==================++++======== Reset Api Call");

            if (`${response.data.status}` === "200") {
                toastsuccessmsg("Password Reset Successfully");
                navigate("../");
            } else {
                toasterrormsg(response.data.message);
            }
        }
    };
    const handlekeydown = (e) => {
        if (e.key === 'Enter') {
            resetpassword_api();
        }
    };
    //call API OVER

    return (
        <>
            {/* <!--begin::Root--> */}
            <div class="d-flex flex-column flex-root" id="kt_app_root">
               
                {/* <!--begin::Authentication - Sign-in --> */}
                <div class="d-flex flex-column flex-lg-row flex-column-fluid">
                    {/* <!--begin::Aside--> */}
                    <div class="d-flex flex-lg-row-fluid">
                        {/* <!--begin::Content--> */}
                        <div class="d-flex flex-column flex-center pb-0 pb-lg-10 p-10 w-100">
                            {/* <!--begin::Image--> */}
                            <img class="theme-light-show mx-auto mw-100 w-150px w-lg-300px mb-10 mb-lg-20" src={backgroundimage} alt="" />
                            
                            {/* <!--end/::Image--> */}
                            {/* <!--begin::Title--> */}
                            <h1 class="text-gray-800 fs-2qx fw-bold text-center mb-7">Fast, Efficient and Productive</h1>
                            {/* <!--end::Title--> */}
                            
                        </div>
                        {/* <!--end::Content--> */}
                    </div>
                    {/* <!--begin::Aside--> */}
                    {/* <!--begin::Body--> */}
                    <div class="d-flex flex-column-fluid flex-lg-row-auto justify-content-center justify-content-lg-end p-12">
                        {/* <!--begin::Wrapper--> */}
                        <div class="bg-body d-flex flex-column flex-center rounded-4 w-md-600px p-10">
                            {/* <!--begin::Content--> */}
                            <div class="d-flex flex-center flex-column align-items-stretch h-lg-100 w-md-400px">
                                {/* <!--begin::Wrapper--> */}
                                <div class="d-flex flex-center flex-column flex-column-fluid pb-10">
                                    {/* <!--begin::Form--> */}
                                    <form class="form w-100" novalidate="novalidate" id="kt_sign_in_form" data-kt-redirect-url="../" action="../">
                                        {/* <!--begin::Heading--> */}
                                        <div class="text-center mb-11">
                                            {/* <!--begin::Title--> */}
                                            <img class="theme-light-show mx-auto mw-100 mb-5" src={logo} alt="" />

                                            <h1 class="text-dark fw-bolder mb-3">Reset Password</h1>
                                            {/* <!--end::Title--> */}
                                            
                                        </div>
                                        {/* <!--begin::Heading--> */}
                                        
                                        {/* <!--begin::Input group=--> */}
                                        <div class="fv-row mb-8">
                                            {/* <!--begin::Email--> */}
                                            <input type="text" placeholder="OTP" name="otp" 
                                                autocomplete="off"
                                                class="form-control bg-transparent"
                                                value={otp}
                                                onChange={(e) => setOTP(e.target.value)}
                                                className="form-control bg-transparent" />
                                            {/* <!--end::Email--> */}
                                        </div>

                                        {/* <!--begin::Input group=--> */}
                                        <div class="fv-row mb-8">
                                            {/* <!--begin::Email--> */}
                                            <input type="password" 
                                            placeholder="Password" 
                                            name="password" 
                                            id="password" 
                                            autocomplete="off" 
                                            className="form-control bg-transparent"
                                            value={password}
                                            onChange={(e) => setPassword(e.target.value)}
                                             />
                                            {/* <!--end::Email--> */}
                                            <span
                                                toggle="#password"
                                                className="fa fa-fw fa-eye field-icon toggle-password"
                                            ></span>
                                        </div>

                                        {/* <!--begin::Input group=--> */}
                                        <div class="fv-row mb-8">
                                            {/* <!--begin::Email--> */}
                                            <input type="password" 
                                            placeholder="Re-type Password" 
                                            id="confirmPassword" 
                                            name="confirmPassword" 
                                            autocomplete="off" 
                                            class="form-control bg-transparent" 
                                            value={confirmPassword}
                                            onChange={(e) => setConfirmPassword(e.target.value)}
                                            />
                                            {/* <!--end::Email--> */}
                                            <span
                                                toggle="#confirmPassword"
                                                className="fa fa-fw fa-eye field-icon toggle-password"
                                            ></span>
                                        </div>
                                        
                                        {/* <!--begin::Wrapper--> */}
                                        <div class="d-flex flex-stack flex-wrap gap-3 fs-base fw-semibold mb-8">
                                            <div></div>
                                            {/* <!--begin::Link--> */}
                                            <Link to="/" className="link-primary">Back To Login</Link>
                                            {/* <!--end::Link--> */}
                                        </div>
                                        {/* <!--end::Wrapper--> */}
                                        {/* <!--begin::Submit button--> */}
                                        <div class="d-grid mb-3">
                                            <Link
                                                onClick={resetpassword_api}
                                                className="btn-save"
                                                type="button"
                                                style={{ color: 'white' }}
                                            >
                                                Reset Password
                                            </Link>
                                        </div>
                                        {/* <!--end::Submit button--> */}
                                        
                                    </form>
                                    {/* <!--end::Form--> */}
                                </div>
                                {/* <!--end::Wrapper--> */}
                                
                            </div>
                            {/* <!--end::Content--> */}
                        </div>
                        {/* <!--end::Wrapper--> */}
                    </div>
                    {/* <!--end::Body--> */}
                </div>
                {/* <!--end::Authentication - Sign-in--> */}
            </div>
            {/* <!--end::Root--> */}
            <ToastContainer />
        </>
    );
}

export default Resetpassword;
