import React, { useEffect, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import { ToastContainer, toast } from "react-toastify";
import Layout from '../Layout/Layout';
import $ from "jquery";
import {
    toasterrormsg,
    toastsuccessmsg,
    Post,
    apiHeader,
    adminSession
} from "../../config/Reausable";

function ProfileData() {
    const [fullName, setFullName] = useState(adminSession.fullName);
    const [mobileNumber, setMobileNumber] = useState(adminSession.mobileNumber);
    const [email, setEmail] = useState(adminSession.email);

    /**
     * CALL UPDATE PROFILE API
     */

    const update_profile_api = async () => {
        //client side validation starts
        var error = 0
        if (error == 0) {
            const data = {
                email: email.trim(),
                fullName: fullName.trim(),
                mobileNumber: mobileNumber.trim(),
                adminId: adminSession.adminId
            };
            const response = await Post('updateProfile', data, apiHeader(false))
            if (`${response.data.status}` === "200") {
                var rdata = response.data['data'][0];
                toastsuccessmsg(response.data.message);
                window.sessionStorage.setItem("admin", JSON.stringify(data));
                setTimeout(() => {
                    window.location.href = "../profile/";
                }, 500);
            } else {
                toasterrormsg(response.data.message);
            }
        }
    };
    const handlekeydown = (e) => {
        if (e.key === 'Enter') {
            update_profile_api();
        }
    };

    /**
     * CALL UPDATE PROFILE API OVER
     */

    return (
        <>
           
            {/* <!--begin::Basic info--> */}
            <div class="card mb-5 mb-xl-10">
                {/* <!--begin::Card header--> */}
                <div class="card-header border-0 cursor-pointer" role="button" data-bs-toggle="collapse" data-bs-target="#kt_account_profile_details" aria-expanded="true" aria-controls="kt_account_profile_details">
                    {/* <!--begin::Card title--> */}
                    <div class="card-title m-0">
                        <h3 class="fw-bold m-0">Profile Details</h3>
                    </div>
                    {/* <!--end::Card title--> */}
                </div>
                {/* <!--begin::Card header--> */}
                {/* <!--begin::Content--> */}
                <div id="kt_account_settings_profile_details" class="collapse show">
                    {/* <!--begin::Form--> */}
                    <form id="kt_account_profile_details_form" class="form">
                        {/* <!--begin::Card body--> */}
                        <div class="card-body border-top p-9">
                            
                            <div class="row mb-6">
                                <label class="col-md-3 col-form-label required fw-semibold fs-6">Full Name</label>
                                <div class="col-md-9">
                                    <div class="row">
                                        <div class="col-md-12 fv-row">
                                            <input 
                                            type="text" 
                                            name="fullName" 
                                            class="form-control form-control-md form-control-solid mb-3" 
                                            placeholder="Name" 
                                                value={fullName}
                                                onChange={(e) => setFullName(e.target.value)}
                                                />
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <div class="row mb-6">
                                <label class="col-md-3 col-form-label required fw-semibold fs-6">Email</label>
                                <div class="col-md-9">
                                    <div class="row">
                                        <div class="col-md-12 fv-row">
                                            <input 
                                            type="text" 
                                            name="email" 
                                            class="form-control form-control-md form-control-solid mb-3" placeholder="Email" 
                                            value={email}
                                            onChange={(e) => setEmail(e.target.value)}
                                                />
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <div class="row mb-6">
                                <label class="col-md-3 col-form-label fw-semibold fs-6">Mobile Number</label>
                                <div class="col-md-9">
                                    <div class="row">
                                        <div class="col-md-12 fv-row">
                                            <input 
                                            type="text" 
                                            name="mobileNumber" 
                                            class="form-control form-control-md form-control-solid mb-3" 
                                            placeholder="Mobile Number" 
                                                value={mobileNumber}
                                                onChange={(e) => setMobileNumber(e.target.value)}
                                            />
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        {/* <!--end::Card body--> */}
                        {/* <!--begin::Actions--> */}
                        <div class="card-footer d-flex justify-content-end p-0">
                            <Link
                                className="btn-cancel"
                                type="button"
                            >
                                Cancel
                            </Link>
                            <Link
                                className="btn-save"
                                type="button"
                                onClick={update_profile_api}
                            >
                                Save
                            </Link>
                            
                        </div>
                        {/* <!--end::Actions--> */}
                    </form>
                    {/* <!--end::Form--> */}
                </div>
                {/* <!--end::Content--> */}
            </div>
            {/* <!--end::Basic info--> */}
                

            <ToastContainer />
        </>
    );
}

function Profile(){
    return <Layout data={<ProfileData />} pageName={"Profile"} />
}

export default Profile;
