import logo from '../../assets/media/logos/logo.png'
import '../../assets/plugins/global/plugins.bundle.css'
import '../../assets/css/style.bundle.css'
import { Link } from "react-router-dom";

function Underconstruction() {
    document.title = 'Buildify AI | Admin | Under Construction'
    return (
        <>
            <div className="d-flex flex-column flex-center flex-column-fluid">
                {/* <!--begin::Content--> */}
                <div className="d-flex flex-column flex-center text-center p-10">
                    {/* <!--begin::Wrapper--> */}
                    <div className="card card-flush w-lg-650px py-5">
                        <div className="card-body py-15 py-lg-20">
                            {/* <!--begin::Title--> */}
                            <h1 className="fw-bolder fs-2qx text-gray-900 mb-4">Under Construction</h1>
                            {/* <!--end::Title--> */}
                            {/* <!--begin::Text--> */}
                            <div className="fw-semibold fs-6 text-gray-500 mb-7">We are building somthing more interesting. Please stay tuned.</div>
                            {/* <!--end::Text--> */}
                            {/* <!--begin::Illustration--> */}
                            <div className="mb-11">
                                <img src={logo} className="mw-100 mh-300px theme-light-show" alt="" />
                            </div>
                            {/* <!--end::Illustration--> */}
                            {/* <!--begin::Link--> */}
                            <div className="mb-0">
                                <a href="../" className="btn btn-primary btn-hover"
                                style={{ color: 'white' }}>Return Home</a>
                            </div>
                            {/* <!--end::Link--> */}
                        </div>
                    </div>
                    {/* <!--end::Wrapper--> */}
                </div>
                {/* <!--end::Content--> */}
            </div>
        </>
    );
}

export default Underconstruction;
