export const fetchFormData = async (url, raw) => {
	try {
		const headers = {
			//   'x-token': token,
			'apitoken': URL.apitoken,
			'Content': 'multipart/form-data',
			//   'Accept': 'application/json'
		};

		// console.log('check api token is', URL.apitoken);


		// var requestOptions =
		// {
		//     method: 'POST',
		//     headers: myHeaders,
		//     body: raw,
		//     redirect: 'follow'
		// };

		let response = await axios.post(URL.test + url, raw, { headers });
		if (response.data.error === "tokenmissmatch") {
			console.log('check error is ', response.data);

			// navigation.push('Login');
			return response.data;
		} else {
			return response.data;
		}
	}
	catch (error) {
		// Handle any request errors here
		console.error('Request error:', error);
		throw error;
	}
}

