export function toasterrormsg(message) {
	toast.error(each_word_capitalize(message.trim()), {
		position: "top-right",
		autoClose: 2000,
		hideProgressBar: false,
		closeOnClick: true,
		pauseOnHover: true,
		draggable: true,
		progress: undefined,
		theme: "dark",
	});
}

export function toastsuccessmsg(message) {
	toast.info(each_word_capitalize(message.trim()), {
		position: "top-right",
		autoClose: 2000,
		hideProgressBar: false,
		closeOnClick: true,
		pauseOnHover: true,
		draggable: true,
		progress: undefined,
		theme: "dark",
	});
}

export const apiHeader = (isFormData) => {

	if (!isFormData) {
		return {
			headers: {
				apitoken: "",
				'Content-Type': 'application/json',
				"Accept": "application/json"
			},
		};
	}

	if (isFormData) {

		return {
			headers: {
				apitoken: "",
				'Content-Type': 'multipart/form-data',
			},
		};
	}

};


export const Post = async (api, data, headers , showAlert = true) => {

	try {
		return await axios.post(URL.test + api, data, headers);
		// return await axios.post(URL.live + api, data, headers);
	} catch (error) {
		console.log("error ==============++==+=======+==+", error.message);

		let networkError = error.message === "Network Error";
		if (showAlert == true) {
			if (networkError === true) {
				console.log('sadasdsad');

				alert(
					error.message,
					"Please Check Your Network Connection",
					[
						{
							text: "OK",
							onPress: () => {
								console.log("OK Pressed");
							},
						},
					],
					{ cancelable: false }
				);
				// <NetworkErrorAlert/>

			} else {
				alert(
					"Submission Errors",
					error.response.data.message,
					[
						{
							text: "OK",
							onPress: () => {
								console.log("OK Pressed");
							},
						},
					],
					{ cancelable: false }
				);
			}
		}
		return undefined;

	}
};


const getSessionData = () => {
	let adminData = window.sessionStorage.getItem('admin')
	let admin = JSON.parse(adminData) ?? ""
	return admin
}
export const adminSession = getSessionData();


const each_word_capitalize = (str) => {
	let sentence = str.toLowerCase().replace(/\b[a-z]/g, function (letter) {
		return letter.toUpperCase();
	})
	return sentence ;
}

const filtereData = (e,allData) => {
	if (e.target.value.length != 0) {
		var str = e.target.value
		const searchData = allData.filter((item) =>
			item.name.toLowerCase().includes(str.toLowerCase())
		)

		return searchData 
	}
	return allData
}
