import React, { useEffect, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import { ToastContainer, toast } from "react-toastify";
import Layout from '../Layout/Layout';
import $ from "jquery";
import {
    toasterrormsg,
    toastsuccessmsg,
    Post,
    apiHeader,
    adminSession
} from "../../config/Reausable";

function UpdatepasswordData() {

    const [oldPassword, setOldPassword] = useState("");
    const [password, setPassword] = useState("");
    const [confirmPassword, setConfirmPassword] = useState("");

    /**
     * CALL UPDATE PASSWORD API
     */
    const navigate = useNavigate();
    const update_password_api = async () => {
        //client side validation starts
        var error = 0
        if (error == 0) {
            const data = {
                oldPassword: oldPassword.trim(),
                password: password.trim(),
                confirmPassword: confirmPassword.trim(),
                adminId: adminSession.adminId
            };
            const response = await Post('updatePassword', data, apiHeader(false))
            if (`${response.data.status}` === "200") {
                toastsuccessmsg(response.data.message);
                setTimeout(() => {
                    window.location.href = "../updatepassword/";
                }, 500);
            } else {
                toasterrormsg(response.message || response.data.message );
            }
        }
    };
    const handlekeydown = (e) => {
        if (e.key === 'Enter') {
            update_password_api();
        }
    };

    /**
     * CALL UPDATE PASSWORD API OVER
     */

    return (
        <>
            
            {/* <!--begin::Basic info--> */}
            <div class="card mb-5 mb-xl-10">
                {/* <!--begin::Card header--> */}
                <div class="card-header border-0 cursor-pointer" role="button" data-bs-toggle="collapse" data-bs-target="#kt_account_profile_details" aria-expanded="true" aria-controls="kt_account_profile_details">
                    {/* <!--begin::Card title--> */}
                    <div class="card-title m-0">
                        <h3 class="fw-bold m-0">Update Password</h3>
                    </div>
                    {/* <!--end::Card title--> */}
                </div>
                {/* <!--begin::Card header--> */}
                {/* <!--begin::Content--> */}
                <div id="kt_account_settings_profile_details" class="collapse show">
                    {/* <!--begin::Form--> */}
                    <form id="kt_account_profile_details_form" class="form">
                        {/* <!--begin::Card body--> */}
                        <div class="card-body border-top p-9">

                            <div class="row mb-6">
                                <label class="col-md-3 col-form-label required fw-semibold fs-6">Old Password</label>
                                <div class="col-md-9">
                                    <div class="row">
                                        <div class="col-md-12 fv-row">
                                            <input 
                                            type="password" 
                                            name="oldPassword" 
                                            id="oldPassword" 
                                            class="form-control form-control-md form-control-solid mb-3"
                                            placeholder="******" 
                                            value={oldPassword}
                                            onChange={(e) => setOldPassword(e.target.value)} />
                                            <span
                                                toggle="#oldPassword"
                                                className="fa fa-fw fa-eye field-icon toggle-password" style={{ marginTop: "-35px"}}
                                            ></span>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <div class="row mb-6">
                                <label class="col-md-3 col-form-label required fw-semibold fs-6">New Password</label>
                                <div class="col-md-9">
                                    <div class="row">
                                        <div class="col-md-12 fv-row">
                                            <input 
                                            type="password" 
                                            name="password"
                                            id="password" 
                                            class="form-control form-control-md form-control-solid mb-3" 
                                            placeholder="******" 
                                            value={password}
                                            onChange={(e) => setPassword(e.target.value)} 
                                            />
                                            <span
                                                toggle="#password"
                                                className="fa fa-fw fa-eye field-icon toggle-password" style={{ marginTop: "-35px" }}
                                            ></span>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <div class="row mb-6">
                                <label class="col-md-3 col-form-label fw-semibold fs-6">Re-type Password</label>
                                <div class="col-md-9">
                                    <div class="row">
                                        <div class="col-md-12 fv-row">
                                            <input
                                                type="password"
                                                name="confirmPassword"
                                                id="confirmPassword"
                                                class="form-control form-control-md form-control-solid mb-3"
                                                placeholder="******"
                                                value={confirmPassword}
                                                onChange={(e) => setConfirmPassword(e.target.value)}
                                                />
                                            <span
                                                toggle="#confirmPassword"
                                                className="fa fa-fw fa-eye field-icon toggle-password" style={{ marginTop: "-35px" }}
                                            ></span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        {/* <!--end::Card body--> */}
                        {/* <!--begin::Actions--> */}
                        <div class="card-footer d-flex justify-content-end p-0">
                            <Link
                                className="btn-cancel"
                                type="button"
                            >
                                Cancel
                            </Link>
                            <Link
                                className="btn-save"
                                type="button"
                                onClick={update_password_api}
                            >
                                Save
                            </Link>

                        </div>
                        {/* <!--end::Actions--> */}
                    </form>
                    {/* <!--end::Form--> */}
                </div>
                {/* <!--end::Content--> */}
            </div>
            {/* <!--end::Basic info--> */}
               
            <ToastContainer />
        </>
    );
}

function Updatepassword() {
    return <Layout data={<UpdatepasswordData />} pageName={"Update Password"} />
}

export default Updatepassword;
