import React, { useEffect, useState, useMemo } from "react";
import { Link, useNavigate } from "react-router-dom";
import $ from "jquery";
import Select from "react-select";
import moment from "moment/moment";
import {
    toasterrormsg,
    toastsuccessmsg,
    Post,
    apiHeader,
    adminSession
} from "../config/Reausable";
import CommonDataTable from "./CommonDataTable";


function Listview({ viewDataHtml, addButtonHtml = "" }) {

    return (
        <>
            <div className="card card-flush">
                <div className="card-header align-items-center py-3 gap-2 gap-md-5 bg-white">
                    <div className="card-title">
                        <div className="d-flex align-items-center position-relative my-1">
                            <i className="ki-outline ki-magnifier fs-3 position-absolute ms-4"></i>&nbsp;
                            <input type="text" data-kt-ecommerce-order-filter="search" className="form-control form-control-solid w-250px ps-12" placeholder="Search Here" />
                        </div>
                    </div>
                    <div className="card-toolbar flex-row-fluid justify-content-end gap-5">
                        <div className="">
                            
                        </div>
                        {addButtonHtml}
                    </div>
                </div>
                <div className="card-body pt-0">
                    {viewDataHtml}
                </div>
            </div> 
        </>
    );
}

export default Listview;
