import React, { useEffect, useState } from "react";
import backgroundimage from '../assets/media/auth/agency-dark.png'
import logo from '../assets/media/logos/logo.png'
import '../assets/plugins/global/plugins.bundle.css'
import '../assets/css/style.bundle.css'
import { Link } from "react-router-dom";
// Import Font Awesome CSS
import '@fortawesome/fontawesome-free/css/all.min.css';

import $ from "jquery";
import {
    Post,
    apiHeader,
    adminSession
} from "../config/Reausable";

function Sidebar() {

    const toggle_menu_accordian = (event) => {
        const clickedElement = event.target;
        let closestElement = $(clickedElement).closest('.menu-accordion') ;
        console.log("result ",$(clickedElement).closest('.menu-accordion'))
        if (closestElement.length > 0){
            $(clickedElement).closest('.menu-accordion').toggleClass("show");
        } else {
            $(".menu-accordion").removeClass('show')
        }
    }

    const toggle_sidebar = () => {
        // $("#kt_app_sidebar_toggle").toggleClass("active");
        // $("#kt_app_sidebar_mobile_toggle").toggleClass("active");
        // $("#kt_app_sidebar").toggleClass("drawer-on")
    }

    /**
     * SET MENUS
     */
    //use state starts
    const [menus, setMenus] = useState([]);

    const menulist_api = async () => {
        const data = {
            adminId: adminSession.adminId
        };
        const response = await Post('menulist', data, apiHeader(false))
        console.log("API Response: ==================++++======== Menu List Api Calll");
        if (`${response.data.status}` === "200") {
            setMenus(response.data['data'])
        }
    };

    useEffect(() => {
        menulist_api()
    }, []);

    return (
        <>
            {/* <!--begin::Sidebar--> */}
            <div id="kt_app_sidebar" className="app-sidebar flex-column" data-kt-drawer="true" data-kt-drawer-name="app-sidebar" data-kt-drawer-activate="{default: true, lg: false}" data-kt-drawer-overlay="true" data-kt-drawer-width="250px" data-kt-drawer-direction="start" data-kt-drawer-toggle="#kt_app_sidebar_mobile_toggle" onClick={toggle_sidebar}>
                <div className="app-sidebar-header d-flex flex-stack d-none d-lg-flex pt-8 pb-2" id="kt_app_sidebar_header">
                    {/* <!--begin::Logo--> */}
                    <Link to="../dashboard/" style={{ textDecoration: "none" }} className="app-sidebar-logo">
                    {/* <a href="#" className="app-sidebar-logo"> */}
                        <img alt="Logo" src={logo} className="h-50px d-none d-sm-inline app-sidebar-logo-default theme-light-show" />
                    {/* </a> */}
                    </Link>
                    {/* <!--end::Logo--> */}
                    {/* <!--begin::Sidebar toggle--> */}
                    <div id="kt_app_sidebar_toggle" className="app-sidebar-toggle btn btn-sm btn-icon bg-light btn-color-gray-700 btn-active-color-primary d-none d-lg-flex rotate" data-kt-toggle="true" data-kt-toggle-state="active" data-kt-toggle-target="body" data-kt-toggle-name="app-sidebar-minimize" onClick={toggle_sidebar}>
                        <i className="ki-outline ki-text-align-right rotate-180 fs-1"></i>
                    </div>
                    {/* <!--end::Sidebar toggle--> */}
                </div>
                {/* <!--begin::Navs--> */}
                <div className="app-sidebar-navs flex-column-fluid py-6" id="kt_app_sidebar_navs">
                    <div id="kt_app_sidebar_navs_wrappers" className="app-sidebar-wrapper hover-scroll-y my-2" data-kt-scroll="true" data-kt-scroll-activate="true" data-kt-scroll-height="auto" data-kt-scroll-dependencies="#kt_app_sidebar_header" data-kt-scroll-wrappers="#kt_app_sidebar_navs" data-kt-scroll-offset="5px">

                        {/* <!--begin::Sidebar menu--> */}
                        <div id="#kt_app_sidebar_menu" data-kt-menu="true" data-kt-menu-expand="false" className="app-sidebar-menu-primary menu menu-column menu-rounded menu-sub-indention menu-state-bullet-primary">
                            <div className="menu-item mb-2">
                                <div className="menu-heading text-uppercase fs-7 fw-bold">Menu</div>
                                <div className="app-sidebar-separator separator"></div>
                            </div>

                            {
                                menus.map((val,index) => {
                                    if (val.submenu.length != 0)
                                    {
                                        return (
                                            <div data-kt-menu-trigger="click" className="menu-item menu-accordion">

                                                <span className="menu-link" onClick={toggle_menu_accordian}>
                                                    <span className="menu-icon">
                                                        <i className={"fs-3 " + val.icon}></i>
                                                    </span>
                                                    <span className="menu-title">{val.menuName}</span>
                                                    <span className="menu-arrow"></span>
                                                </span>

                                                <div className="menu-sub menu-sub-accordion">
                                                    <div data-kt-menu-trigger="click" className="menu-item menu-accordion">
                                                        {
                                                            val.submenu.map((subval, index) => {
                                                                return(
                                                                    <span className="menu-link">
                                                                        <Link className="menu-bullet" to={subval.path}>
                                                                            <span className="bullet bullet-dot"></span>
                                                                        </Link>
                                                                        <Link className="menu-title" to={subval.path}>{subval.menuName}</Link>
                                                                        {
                                                                            (subval.add == "YES") ? <Link className="menu-icon" to={subval.addPath}><i className="fa fa-plus-square fs-3"></i></Link> : ""
                                                                        }
                                                                    </span>
                                                                )
                                                            })
                                                        }
                                                    </div>
                                                </div>
                                            </div>
                                        )
                                    }
                                    else
                                    {
                                        return (
                                            <div data-kt-menu-trigger="click" className="menu-item here">
                                                <span className="menu-link" onClick={toggle_menu_accordian}>
                                                    <Link className="menu-icon" to={val.path}>
                                                        <i className={"fs-3 "+val.icon}></i>
                                                    </Link>
                                                    <Link className="menu-title" to={val.path}>{val.menuName}</Link>
                                                    { 
                                                        (val.add == "YES") ? <Link className="menu-icon" to={val.addPath}><i className="fa fa-plus-square fs-3" ></i></Link> : ""
                                                    }
                                                </span>
                                            </div>
                                        )
                                    }
                                })
                            }
                        </div>
                        {/* <!--end::Sidebar menu--> */}
                    </div>
                </div>
                {/* <!--end::Navs--> */}
            </div>
            {/* <!--end::Sidebar--> */}
        </>
    );
}

export default Sidebar;
