import React, { useEffect, useState, useMemo } from "react";
import { Link, useNavigate } from "react-router-dom";
import $ from "jquery";
import Select from "react-select";
import Layout from '../Layout/Layout';
import moment from "moment/moment";
import {
    toasterrormsg,
    toastsuccessmsg,
    Post,
    apiHeader,
    adminSession
} from "../../config/Reausable";
import CommonDataTable from "../../component/CommonDataTable";
import CommonDatatableAction from "../../component/CommonDatatableAction";
import Listview from '../../component/Listview';


function CategoryData() {

    const [tableData, setTableData] = useState([]);

    // const [optionValue, setOptionValue] = useState([]);
    const categorylist_api = async () => {
        const data = {
            accountType: 'OWNER',
        };
        const response = await Post('categorylist', data, apiHeader(false))
        if (`${response.data.status}` === "200") {
            setTableData(response.data.data)
        }
    };

    useEffect(() => {
        categorylist_api()
    }, []);
    //toggle theme mode over

    const renderAddCategory = () => {
        return (
            <>
                <Link to="../category" className="btn-save">Add Category</Link>
            </>
        );
    };

    // Columns List
    const columns = useMemo(
        () => [
            {
                name: "S No",
                selector: (row, index) => index + 1,
                sortable: true,
                filter: true,
            },
            {
                name: "Category",
                selector: (row) => row.name,
                sortable: true,
                filter: true,
            },
            {
                name: "Date Created",
                selector: (row) => {
                    const formattedDate = moment(row.created).format("DD/MM/YYYY");
                    return formattedDate;
                },
                sortable: true,
                filter: true,
            },
            {
                name: "Date Updated",
                selector: (row) => {
                    const formattedDate = moment(row.updated).format("DD/MM/YYYY");
                    return formattedDate;
                },
                sortable: true,
                filter: true,
            },
            {
                name: "Action",
                selector : (row) => {
                    let actions = [
                        {
                            "lable" : "Edit",
                            "redirect": "../category",
                            "primarykey": row.categoryId,
                            "onClick" : ""
                        },
                        {
                            "lable": "Delete",
                            "data": row,
                            "onClick": ""
                        },
                    ]
                    return (<CommonDatatableAction action={actions} />)
                }
            },
        ],
        []
    );


    return (
        <>
            <Listview
                viewDataHtml={<CommonDataTable columns={columns} tableData={tableData} />}
                addButtonHtml={renderAddCategory()}
            />

        </>
    );
}

function Categorylist() {
    return <Layout data={<CategoryData />} pageName={"Category List"} />
}

export default Categorylist;
