import React, { useEffect, useState, useMemo } from "react";
import { Link, useNavigate } from "react-router-dom";
import $ from "jquery";
import Select from "react-select";
import moment from "moment/moment";
import {
    toasterrormsg,
    toastsuccessmsg,
    Post,
    apiHeader,
    adminSession
} from "../config/Reausable";
import CommonDataTable from "./CommonDataTable";
import Listview from "./Listview";
import CommonDatatableAction from "./CommonDatatableAction"


function UserData({ accountType = "OWNER" }) {

    const [tableData, setTableData] = useState([]);

    const tableTitle = () => {
        if (accountType == "OWNER"){
            return "Owner List"
        } else if (accountType == "BUILDER"){
            return "Builder List"
        } else {
            return "Subcontractor List"
        }
    }

    // const [optionValue, setOptionValue] = useState([]);
    const userlist_api = async () => {
        const data = {
            accountType: accountType,
        };
        const response = await Post('userlist', data, apiHeader(false))
        if (`${response.data.status}` === "200") {
            setTableData(response.data.data)
        }
    };

    useEffect(() => {
        userlist_api()
    }, []);
    //toggle theme mode over

    const toggle_action_menu = (event) => {
        const clickedElement = event.target;
        let closestElement = $(clickedElement).closest('.action');
        $(clickedElement).toggleClass("show menu-dropdown");
        $(closestElement).find('.toggle-action-options').toggleClass("show showactionmenu");
    }

    const renderAddUser = () => {
        // if (accountType === "OWNER") {
        //     return (
        //         <>
        //             <Link to="#" className="btn-save">Add Owner</Link>
        //         </>
        //     );
        // }
        // else if (accountType === "BUILDER") {
        //     return (
        //         <>
        //             <Link to="#" className="btn-save">Add Builder</Link>
        //         </>
        //     );
        // }
        // else {
        //     return (
        //         <>
        //             <Link to="#" className="btn-save">Add Sub Contractor</Link>
        //         </>
        //     );
        // }
        return null;
    };

    // Columns List
    const columns = useMemo(
        () => [
            {
                name: "S No",
                selector: (row, index) => index + 1,
                sortable: true,
                filter: true,
            },
            // {
            //     name: renderCheckBox(),
            //     cell: (row) => renderCheckBox(),
            // },
            {
                name: "Email",
                selector: (row) => row.email,
                sortable: true,
                filter: true,
            },
            {
                name: "Mobile No",
                selector: (row) => row.mobileNumber,
                sortable: true,
                filter: true,
            },
            {
                name: "Name",
                selector: (row) => row.fullName,
                sortable: true,
                filter: true,
            },
            {
                name: "Date Created",
                selector: (row) => {
                    const formattedDate = moment(row.created).format("DD/MM/YYYY");
                    return formattedDate;
                },
                sortable: true,
                filter: true,
            },
            {
                name: "Date Updated",
                selector: (row) => {
                    const formattedDate = moment(row.updated).format("DD/MM/YYYY");
                    return formattedDate;
                },
                sortable: true,
                filter: true,
            },
            {
                name: "Action",
                // cell: (row) => <Link>View</Link>,
                selector: (row) => {
                    let actions = [
                        {
                            "lable": "View",
                            "redirect": "../userdetail",
                            "data": row,
                            "onClick": ""
                        },
                        {
                            "lable": "Edit",
                            "redirect": "../user",
                            "data": row,
                            "onClick": ""
                        },
                        {
                            "lable": "Delete",
                            "data": row,
                            "onClick": ""
                        },
                    ]
                    return (<CommonDatatableAction action={actions} />)
                }
            },
        ],
        []
    );


    return (
        <>
            <Listview 
                viewDataHtml={<CommonDataTable columns={columns} tableData={tableData} />}
                addButtonHtml={renderAddUser()}
            />
            
        </>
    );
}

export default UserData;
