import React, { useEffect, useState, useMemo } from "react";
import { Link, useNavigate, useLocation } from "react-router-dom";
import $ from "jquery";
import Select from "react-select";
import Layout from '../Layout/Layout';
import moment from "moment/moment";
import { ToastContainer, toast } from "react-toastify";
import {
    toasterrormsg,
    toastsuccessmsg,
    Post,
    apiHeader,
    adminSession
} from "../../config/Reausable";
import CommonDataTable from "../../component/CommonDataTable";
import CommonDatatableAction from "../../component/CommonDatatableAction";
import Listview from '../../component/Listview';


function CategoryData() {
    const location = useLocation();
    const navigate = useNavigate();

    let categoryId = location.state ? location.state.primarykey : "";
    console.log("categoryId ========",categoryId)
    const [formData, setFormData] = useState({
        name : ""
    });

    const handleInputChange = (e, fieldName) => {
        setFormData((prevData) => ({
            ...prevData,
            [fieldName]: e.target.value,
        }));
    };

    useEffect(() => {
        if(categoryId != "")
        {
            get_category_detail_api()
        }
    }, []);
    
    const get_category_detail_api = async  () => {
        const data = {
            categoryId: categoryId
        };
        const response = await Post('categoryDetail', data, apiHeader(false))
        let existingData = response.data.data[0];
        console.log(existingData);
        setFormData({
            name : existingData.name
        })
    }

    const save_category_api = async () => {
        var error = 0
        if (formData.name.trim() == "") {
            error = 1;
            toasterrormsg("Category Name Required");
        } 
        formData["adminId"] = adminSession.adminId
        if(categoryId !== ""){
            formData["categoryId"] = categoryId
        }

        console.log(formData);
        
        if (error == 0) {
            const response = await Post('saveCategory', formData, apiHeader(false))
            if (`${response.data.status}` === "200") {
                toastsuccessmsg(response.data.message);
                setTimeout(() => {
                    window.location.href = "../categorylist";
                }, 500);
            } else {
                toasterrormsg(response.data.message);
            }
        }
    }


    

    return (
        <>
            <div class="card mb-5 mb-xl-10">
                <div class="card-header border-0 cursor-pointer" role="button" data-bs-toggle="collapse" data-bs-target="#kt_account_profile_details" aria-expanded="true" aria-controls="kt_account_profile_details">
                    <div class="card-title m-0">
                        <h3 class="fw-bold m-0">{(categoryId != "") ? "Edit" : "Add"} Category</h3>
                    </div>
                </div>
                {/* <!--begin::Content--> */}
                <div id="kt_account_settings_profile_details" class="collapse show">
                    <form id="kt_account_profile_details_form" class="form">
                        <div class="card-body border-top p-9">
                            <div class="row">
                                
                                <div class="col-md-4">
                                    <label class="col-md-3 col-form-label required fw-semibold fs-6">Name</label>
                                    <div class="mb-2">
                                        <input
                                            type="text"
                                            class="form-control form-control-md form-control-solid mb-3"
                                            placeholder="Category Name"
                                            value={formData.name}
                                            onChange={(e) => handleInputChange(e, 'name')}
                                        />
                                    </div>
                                </div>
                            </div>
                        </div>
                        {/* <!--begin::Actions--> */}
                        <div class="card-footer d-flex justify-content-end p-0">
                            <Link
                                className="btn-cancel"
                                type="button"
                            >
                                Cancel
                            </Link>
                            <Link
                                className="btn-save"
                                type="button"
                                onClick={save_category_api}
                            >
                                Save
                            </Link>

                        </div>
                    </form>
                </div>
                {/* <!--end::Content--> */}
            </div>
            <ToastContainer />
        </>
    );
}

function CategoryForm() {
    return <Layout data={<CategoryData />} pageName={"Category"} />
}

export default CategoryForm;
