import React, { useEffect, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import $ from "jquery";

function Footer() {

    //toggle password button
    useEffect(() => {
        $(".toggle-password").on("click", function () {
            $(this).toggleClass("fa-eye fa-eye-slash");
            var input = $($(this).attr("toggle"));
            if (input.attr("type") === "password") {
                input.attr("type", "text");
            } else {
                input.attr("type", "password");
            }
        });
        return () => {
            $(".toggle-password").off("click");
        };
    }, []);
    //toggle password button over

    return (
        <>
            {/* <!--begin::Footer--> */}
            <div id="kt_app_footer" className="app-footer">
                {/* <!--begin::Footer container--> */}
                <div className="app-container container-fluid d-flex flex-column flex-md-row flex-center flex-md-stack py-3">
                    {/* <!--begin::Copyright--> */}
                    <div className="text-dark order-2 order-md-1">
                        <span className="text-muted fw-semibold me-1">2023&copy;</span>
                        <Link to="https://xpertlab.com/" target="_blank" className="text-gray-800 text-hover-success">Xpertlab Technologies Private Limited</Link>
                    </div>
                    {/* <!--end::Copyright--> */}
                    
                </div>
                {/* <!--end::Footer container--> */}
            </div>
            {/* <!--end::Footer--> */}
        </>
    );
}

export default Footer;
