import React, { useEffect, useState } from "react";
import backgroundimage from '../assets/media/auth/agency-dark.png'
import logo from '../assets/media/logos/logo.png'
import '../assets/plugins/global/plugins.bundle.css'
import '../assets/css/style.bundle.css'
import { Link, useNavigate } from "react-router-dom";
import { ToastContainer, toast } from "react-toastify";
import $ from "jquery";
import {
    toasterrormsg,
    toastsuccessmsg,
    Post,
    apiHeader
} from "../config/Reausable";


function SignIn() {
    document.title = 'Buildify AI | Admin | Login'

    /**
     * VALIDATION
     * DEFAULT VALUE SET
     * API CALL
     */

    //toggle password button
    useEffect(() => {
        $(".toggle-password").on("click", function () {
            $(this).toggleClass("fa-eye fa-eye-slash");
            var input = $($(this).attr("toggle"));
            if (input.attr("type") === "password") {
                input.attr("type", "text");
            } else {
                input.attr("type", "password");
            }
        });
        return () => {
            $(".toggle-password").off("click");
        };
    }, []);
    //toggle password button over

    //use state starts
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    //use state over

    //call API
    const navigate = useNavigate();
    const signin_api = async () => {

        //client side validation starts
        var error = 0
        if(email.trim() == ""){
            error = 1;
            toasterrormsg("Email Required");
        }
        else if (password.trim() == ""){
            error = 1;
            toasterrormsg("Password Required");
        }
        if (error == 0)
        {
            const data = {
                email: email,
                password: password,
            };
            const response = await Post('signin',data, apiHeader(false))
            console.log("API Response: ==================++++======== Login Api Calll", response);
            if (`${response.data.status}` === "200") {
                var rdata = response.data['data'][0];
                window.sessionStorage.setItem("admin", JSON.stringify(rdata));
                toastsuccessmsg(response.data.message);
                setTimeout(() => {
                    window.location.href = "../dashboard/";
                }, 500);
            } else {
                toasterrormsg(response.data.message);
            }
        }
    };
    const handlekeydown = (e) => {
        if (e.key === 'Enter') {
            signin_api();
        }
    };
    //call API OVER
    return (
        <>
            {/* <!--begin::Root--> */}
            <div class="d-flex flex-column flex-root p-10" id="kt_app_root">
               
                {/* <!--begin::Authentication - Sign-in --> */}
                <div class="d-flex flex-column flex-lg-row flex-column-fluid">
                    {/* <!--begin::Aside--> */}
                    <div class="d-flex flex-lg-row-fluid">
                        {/* <!--begin::Content--> */}
                        <div class="d-flex flex-column flex-center pb-0 pb-lg-10 p-10 w-100">
                            {/* <!--begin::Image--> */}
                            <img class="theme-light-show mx-auto mw-100 w-150px w-lg-300px mb-10 mb-lg-20" src={backgroundimage} alt="" />
                            
                            {/* <!--end/::Image--> */}
                            {/* <!--begin::Title--> */}
                            <h1 class="text-gray-800 fs-2qx fw-bold text-center mb-7">Fast, Efficient and Productive</h1>
                            {/* <!--end::Title--> */}
                            
                        </div>
                        {/* <!--end::Content--> */}
                    </div>
                    {/* <!--begin::Aside--> */}
                    {/* <!--begin::Body--> */}
                    <div class="d-flex flex-column-fluid flex-lg-row-auto justify-content-center justify-content-lg-end p-12">
                        {/* <!--begin::Wrapper--> */}
                        <div class="bg-body d-flex flex-column flex-center rounded-4 w-md-600px p-10">
                            {/* <!--begin::Content--> */}
                            <div class="d-flex flex-center flex-column align-items-stretch h-lg-100 w-md-400px">
                                {/* <!--begin::Wrapper--> */}
                                <div class="d-flex flex-center flex-column flex-column-fluid pb-10">
                                    {/* <!--begin::Form--> */}
                                    <form class="form w-100" novalidate="novalidate">
                                        {/* <!--begin::Heading--> */}
                                        <div class="text-center mb-11">
                                            {/* <!--begin::Title--> */}
                                            <img class="theme-light-show mx-auto mw-100 mb-5" src={logo} alt="" />

                                            <h1 class="text-dark fw-bolder mb-3">Sign In</h1>
                                            {/* <!--end::Title--> */}
                                            
                                        </div>
                                        {/* <!--begin::Heading--> */}
                                        
                                        {/* <!--begin::Input group=--> */}
                                        <div class="fv-row mb-8">
                                            {/* <!--begin::Email--> */}
                                            <input type="text" 
                                                placeholder="Email" 
                                                name="email" 
                                                autocomplete="off" 
                                                class="form-control bg-transparent"
                                                value={email}
                                                onChange={(e) => setEmail(e.target.value)}
                                            />
                                            {/* <!--end::Email--> */}
                                        </div>
                                        {/* <!--end::Input group=--> */}
                                        <div class="fv-row mb-3">
                                            {/* <!--begin::Password--> */}
                                            <input type="password" 
                                                placeholder="Password" 
                                                name="password" 
                                                autocomplete="off" 
                                                class="form-control bg-transparent" 
                                                value={password}
                                                onChange={(e) => setPassword(e.target.value)}
                                                id="password"
                                             />
                                            <span
                                                toggle="#password"
                                                className="fa fa-fw fa-eye field-icon toggle-password"
                                            ></span>
                                            {/* <!--end::Password--> */}
                                        </div>
                                        {/* <!--end::Input group=--> */}
                                        {/* <!--begin::Wrapper--> */}
                                        <div class="d-flex flex-stack flex-wrap gap-3 fs-base fw-semibold mb-8">
                                            <div></div>
                                            {/* <!--begin::Link--> */}
                                            <Link to="/forgotpassword/" className="link-primary">Forgot Password ?</Link>
                                            {/* <!--end::Link--> */}
                                        </div>
                                        {/* <!--end::Wrapper--> */}
                                        {/* <!--begin::Submit button--> */}
                                        <div class="d-grid mb-10">
                                            <Link
                                                onClick={signin_api}
                                                className="btn-save"
                                                type="button"
                                                style={{ color: 'white' }}
                                            >
                                                Sign In
                                            </Link>
                                        </div>
                                        {/* <!--end::Submit button--> */}
                                        
                                    </form>
                                    {/* <!--end::Form--> */}
                                </div>
                                {/* <!--end::Wrapper--> */}
                                
                            </div>
                            {/* <!--end::Content--> */}
                        </div>
                        {/* <!--end::Wrapper--> */}
                    </div>
                    {/* <!--end::Body--> */}
                </div>
                {/* <!--end::Authentication - Sign-in--> */}
            </div>
            {/* <!--end::Root--> */}
            <ToastContainer />
        </>
    );
}

export default SignIn;
