import React, { useEffect, useState, useMemo } from "react";
import { Link, useNavigate } from "react-router-dom";

function CommonDatatableAction({ action = []}) {
    return (
        <div>
            <button
                className="bg-red border-0"
                type="button"
                id="dropdownMenuButton1"
                data-bs-toggle="dropdown"
                aria-expanded="false"

            >
                <i className="fa fa-ellipsis-v"></i>
            </button>

            <ul className={"dropdown-menu position-fixed p-0"} aria-labelledby="dropdownMenuButton1">
                <li className="p-2">
                    {/* <Link className="dropdown-item" to="" >View</Link> */}
                    {/* <Link className="dropdown-item" to="" >View</Link> */}
                    {
                        action.map((val, index) => {
                            if (val.lable === "Edit") {
                                return(<Link className="dropdown-item text-hover-success font-bold" to={(val.redirect && val.redirect !== "") ? val.redirect : "javascript:void(0)"} key={index} state={{primarykey : val.primarykey}}>{val.lable}</Link>)
                            } else {
                                return (
                                    <Link className="dropdown-item text-hover-success font-bold" key={index}>{val.lable}</Link>
                                )
                            }
                        })
                    }
                </li>
            </ul>
        </div>
    )
}

export default CommonDatatableAction;
