import React, { useEffect, useState } from "react";
import userImage from '../assets/media/avatars/300-2.jpg'
import logo from '../assets/media/logos/logo.png'
import '../assets/plugins/custom/datatables/datatables.bundle.css'
import '../assets/plugins/custom/vis-timeline/vis-timeline.bundle.css'
import '../assets/plugins/global/plugins.bundle.css'
import '../assets/css/style.bundle.css'
import { Link, useNavigate } from "react-router-dom";
import $ from "jquery";
import {
    Post,
    apiHeader,
    adminSession
} from "../config/Reausable";
import SweetAlert from "react-bootstrap-sweetalert";



function Header({pageName = "", action=""}) {

    const [showLogout, setshowLogout] = useState(false);
    const navigate = useNavigate();
    if (adminSession == ""){
        navigate("../");
    }
    
    const toggle_menu_profile = (event) => {
        $("#profilemenu_1").toggleClass('show menu-dropdown')
        $("#profilemenu_2").toggleClass('show showprofilemenu')
    }

    // Logout functionality 
    const logout = () => {
        setshowLogout(true)
    }
    
    // Cancel Click
    const onCancel = () => {
        setshowLogout(false)
    }

    // Confirm Click
    const onConfirm = () => {
        setshowLogout(false)
        window.sessionStorage.removeItem('admin')
        window.history.replaceState(null, 'Login', './login');
        window.location.href = "../";
    }

    useEffect(() => {
        var defaultThemeMode = "light"; 
        var themeMode; 
        if (document.documentElement) { 
            if (document.documentElement.hasAttribute("data-bs-theme-mode")) { 
                themeMode = document.documentElement.getAttribute("data-bs-theme-mode"); 
            } else { 
                if (localStorage.getItem("data-bs-theme") !== null) { 
                    themeMode = localStorage.getItem("data-bs-theme"); 
                } else { 
                    themeMode = defaultThemeMode; 
                } 
            } 
            if (themeMode === "system") { 
                themeMode = window.matchMedia("(prefers-color-scheme: dark)").matches ? "dark" : "light"; 
            } 
            document.documentElement.setAttribute("data-bs-theme", themeMode); 
        }
        //SET PAGE NAME
        document.title = `Buildify AI | Admin | ${pageName}`;
        //SET PAGE NAME OVER

    }, []);
    //toggle theme mode over

    const renderAction = () => {
        if (action !== "") {
            return (
                <>
                    <li className="breadcrumb-item">
                        <span className="bullet bg-gray-400 w-5px h-2px"></span>
                    </li>
                    <li className="breadcrumb-item text-muted">{action}</li>
                </>
            );
        }
        return null;
    };

    const renderPageName = () => {
        if (pageName !== "" && pageName !== 'Dashboard') {
            return (
                <>
                    <li className="breadcrumb-item">
                        <span className="bullet bg-gray-400 w-5px h-2px"></span>
                    </li>
                    <li className="breadcrumb-item text-muted  fs-5">{pageName}</li>
                </>
            );
        }
        return null;
    };


    return (
        <>
            {/* <!--begin::Header--> */}
            <div id="kt_app_header" className="app-header">
                {/* <!--begin::Header container--> */}
                <div className="app-container container-fluid d-flex align-items-stretch flex-stack" id="kt_app_header_container">
                    {/* <!--begin::Sidebar toggle--> */}
                    <div className="d-flex align-items-center d-block d-lg-none ms-n3" title="Show sidebar menu">
                        <div className="btn btn-icon btn-active-color-primary w-35px h-35px me-2" id="kt_app_sidebar_mobile_toggle">
                            <i className="ki-outline ki-abstract-14 fs-2"></i>
                        </div>
                        {/* <!--begin::Logo image--> */}
                        <Link to="../dashboard/" style={{textDecoration:"none"}}>
                            <img alt="Logo" src={logo} className="h-30px" />
                        </Link>
                        {/* <!--end::Logo image--> */}
                    </div>
                    {/* <!--end::Sidebar toggle--> */}
                    {/* <!--begin::Navbar--> */}
                    <div className="app-navbar flex-lg-grow-1" id="kt_app_header_navbar">
                        <div className="app-navbar-item d-flex align-items-stretch flex-lg-grow-1">
                            <div id="kt_app_toolbar_container" className="app-container container-fluid d-flex align-items-stretch">
                                <div className="app-toolbar-wrapper d-flex flex-stack flex-wrap gap-4 w-100">
                                    <div className="page-title d-flex flex-column justify-content-center gap-1 me-3">
                                        {/* <h1 className="page-heading d-flex flex-column justify-content-center text-dark fw-bold fs-3 m-0">{pageName}</h1> */}

                                        {/* Conditionally render the ul only if pageName is not "Dashboard" */}
                                        {pageName && (
                                            <ul className="breadcrumb breadcrumb-separatorless fw-semibold fs-7 my-0">
                                                <li className="breadcrumb-item text-muted">
                                                    <Link to="../dashboard/" className="text-muted text-hover-success fs-5" style={{textDecoration:"none"}}>
                                                        Dashboard
                                                    </Link>
                                                </li>
                                                {renderPageName()}
                                                {renderAction()}
                                            </ul>
                                        )}
                                        
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div className="app-navbar-item ms-1 ms-md-3" id="kt_header_user_menu_toggle" onClick={toggle_menu_profile}>

                            <div className="cursor-pointer symbol symbol-circle symbol-35px symbol-md-45px" data-kt-menu-trigger="{default: 'click', lg: 'hover'}" data-kt-menu-attach="parent" data-kt-menu-placement="bottom-end" id="profilemenu_1">
                                <img src={userImage} alt="user" />
                            </div>

                            <div className="menu menu-sub menu-sub-dropdown menu-column menu-rounded menu-gray-800 menu-state-bg menu-state-color fw-semibold py-4 fs-6 w-275px" data-kt-menu="true" id="profilemenu_2">
                                <div className="menu-item px-3">
                                    <div className="menu-content d-flex align-items-center">
                                        <div className="symbol symbol-50px me-3">
                                            <img alt="Logo" src={userImage} />
                                        </div>
                                        <div className="d-flex flex-column">
                                            <div className="fw-bold d-flex align-items-center fs-5">{adminSession.fullName}
                                                {/* <span className="badge badge-light-success fw-bold fs-8 px-2 py-1 ms-2">Pro</span> */}
                                            </div>
                                            <a href="#" className="fw-semibold text-muted text-hover-success fs-7">{adminSession.email}</a>
                                        </div>

                                    </div>
                                </div>

                                <div className="separator my-2"></div>

                                <div className="menu-item px-2">
                                    <Link className="menu-link" to="../profile/">
                                        <i className="fa fa-power-off fs-6"></i>&nbsp;&nbsp; My Profile
                                    </Link>
                                </div>

                                <div className="menu-item px-2">
                                    <Link className="menu-link" to="../updatepassword/">
                                        <i className="fa fa-power-off fs-6"></i>&nbsp;&nbsp; Update Password
                                    </Link>
                                </div>

                                <div className="menu-item px-2">
                                    <Link className="menu-link" onClick={() => { logout() }}>
                                        <i className="fa fa-power-off fs-6"></i>&nbsp;&nbsp; Sign Out
                                    </Link>
                                </div>

                            </div>
                        </div>
                    </div>
                    {/* <!--end::Navbar--> */}
                    {/* <!--begin::Separator--> */}
                    <div className="app-navbar-separator separator d-none d-lg-flex"></div>
                    {/* <!--end::Separator--> */}
                </div>
                {/* <!--end::Header container--> */}
            </div>
            {/* <!--end::Header--> */}

            {showLogout && (
                <SweetAlert
                    show={showLogout}
                    custom
                    showCancel
                    showCloseButton
                    confirmBtnText="Yes"
                    cancelBtnText="No"
                    confirmBtnBsStyle="primary"
                    cancelBtnBsStyle="light"
                    title="Logout!"
                    onConfirm={onConfirm}
                    onCancel={onCancel}
                    confirmBtnCssClass="btn-save text-white"  // Custom class for the confirm button
                    cancelBtnCssClass="btn-cancel text-white"  // Custom class for the cancel button
                    
                >
                    Are you sure you want to logout?
                </SweetAlert>
            )}
        </>
    );
}

export default Header;
