import React, { useEffect, useState } from "react";
import backgroundimage from '../assets/media/auth/agency-dark.png'
import logo from '../assets/media/logos/logo.png'
import '../assets/plugins/global/plugins.bundle.css'
import '../assets/css/style.bundle.css'
import { Link, useNavigate } from "react-router-dom";
import { ToastContainer, toast } from "react-toastify";
import $ from "jquery";
import {
    toasterrormsg,
    toastsuccessmsg,
    Post,
    apiHeader
} from "../config/Reausable";

function Forgotpassword() {
    document.title = 'Buildify AI | Admin | Forgot Password'

    /**
   * VALIDATION
   * DEFAULT VALUE SET
   * API CALL
   */

    //use state starts
    const [email, setEmail] = useState("");
    //use state over

    //call API
    const navigate = useNavigate();
    const forgotpassword_api = async () => {

        //client side validation starts
        var error = 0
        if (email.trim() == "") {
            error = 1;
            toasterrormsg("Email Required");
        }
        if (error == 0) {

            const data = {
                email: email
            };
            const response = await Post('forgotpassword', data, apiHeader(false))
            console.log("API Response: ==================++++======== Forgot Api Call", response);
            var rdata = response.data['data'][0] ;
            if (`${response.data.status}` === "200") {
                navigate("../resetpassword", { state: rdata.adminId});
            } else {
                toasterrormsg(response.data.message);
            }
        }
    };
    const handlekeydown = (e) => {
        if (e.key === 'Enter') {
            forgotpassword_api();
        }
    };
    //call API OVER

    return (
        <>
            {/* <!--begin::Root--> */}
            <div class="d-flex flex-column flex-root p-10" id="kt_app_root">
               
                {/* <!--begin::Authentication - Sign-in --> */}
                <div class="d-flex flex-column flex-lg-row flex-column-fluid">
                    {/* <!--begin::Aside--> */}
                    <div class="d-flex flex-lg-row-fluid">
                        {/* <!--begin::Content--> */}
                        <div class="d-flex flex-column flex-center pb-0 pb-lg-10 p-10 w-100">
                            {/* <!--begin::Image--> */}
                            <img class="theme-light-show mx-auto mw-100 w-150px w-lg-300px mb-10 mb-lg-20" src={backgroundimage} alt="" />
                            
                            {/* <!--end/::Image--> */}
                            {/* <!--begin::Title--> */}
                            <h1 class="text-gray-800 fs-2qx fw-bold text-center mb-7">Fast, Efficient and Productive</h1>
                            {/* <!--end::Title--> */}
                            
                        </div>
                        {/* <!--end::Content--> */}
                    </div>
                    {/* <!--begin::Aside--> */}
                    {/* <!--begin::Body--> */}
                    <div class="d-flex flex-column-fluid flex-lg-row-auto justify-content-center justify-content-lg-end p-12">
                        {/* <!--begin::Wrapper--> */}
                        <div class="bg-body d-flex flex-column flex-center rounded-4 w-md-600px p-10">
                            {/* <!--begin::Content--> */}
                            <div class="d-flex flex-center flex-column align-items-stretch h-lg-100 w-md-400px">
                                {/* <!--begin::Wrapper--> */}
                                <div class="d-flex flex-center flex-column flex-column-fluid pb-10">
                                    {/* <!--begin::Form--> */}
                                    <form class="form w-100" novalidate="novalidate" id="kt_sign_in_form" data-kt-redirect-url="../resetpassword" action="../resetpassword">
                                        {/* <!--begin::Heading--> */}
                                        <div class="text-center mb-11">
                                            {/* <!--begin::Title--> */}
                                            <img class="theme-light-show mx-auto mw-100 mb-5" src={logo} alt="" />

                                            <h1 class="text-dark fw-bolder mb-3">Forgot Password?</h1>
                                            {/* <!--end::Title--> */}
                                            
                                        </div>
                                        {/* <!--begin::Heading--> */}
                                        
                                        {/* <!--begin::Input group=--> */}
                                        <div class="fv-row mb-8">
                                            {/* <!--begin::Email--> */}
                                            <input type="text"
                                                placeholder="Email"
                                                name="email"
                                                autocomplete="off"
                                                class="form-control bg-transparent"
                                                value={email}
                                                onChange={(e) => setEmail(e.target.value)}
                                            />
                                            {/* <!--end::Email--> */}
                                        </div>
                                        
                                        {/* <!--begin::Wrapper--> */}
                                        <div class="d-flex flex-stack flex-wrap gap-3 fs-base fw-semibold mb-8">
                                            <div></div>
                                            {/* <!--begin::Link--> */}
                                            <Link to="/" className="link-primary">Back To Login</Link>
                                            {/* <!--end::Link--> */}
                                        </div>
                                        {/* <!--end::Wrapper--> */}
                                        {/* <!--begin::Submit button--> */}
                                        <div class="d-grid mb-10">

                                            <Link
                                                onClick={forgotpassword_api}
                                                className="btn-save"
                                                type="button"
                                                style={{ color: 'white' }}
                                            >
                                                Send OTP
                                            </Link>
                                        </div>
                                        {/* <!--end::Submit button--> */}
                                        
                                    </form>
                                    {/* <!--end::Form--> */}
                                </div>
                                {/* <!--end::Wrapper--> */}
                                
                            </div>
                            {/* <!--end::Content--> */}
                        </div>
                        {/* <!--end::Wrapper--> */}
                    </div>
                    {/* <!--end::Body--> */}
                </div>
                {/* <!--end::Authentication - Sign-in--> */}
            </div>
            {/* <!--end::Root--> */}
            <ToastContainer />
        </>
    );
}

export default Forgotpassword;
